@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
/*Global styles*/

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    color: #333333;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

button, input {
    text-decoration: none;
    outline: 0;
}
/*Main and SideBar*/

.main_content {
  display: flex;
  max-width: 1440px;
  margin: 0 auto;
}

@media (max-width: 960px) {
  .main_content {
    display: block;
    position: relative;
  }
}
/*Logo*/

.logo {
    display: flex;
    justify-content: space-between;
    padding-bottom: 78px;
}

.logo_title {
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;
}

/*Intro*/

.intro_title {
    font-weight: bold;
    font-size: 32px;
    line-height: 44px;
}

.intro_subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 44px;
    margin-bottom: 43px;
}
/*Rectangle with a joke*/

.rectangle {
    display: flex;
    flex-direction: column;
    padding: 40px;
    margin-top: 20px;
    background: #F8F8F8;
    border-radius: 20px;
}

/*Favourite icon*/

.favorite_joke {
    align-self: flex-end;
    border: none;
    cursor: pointer;
    background-color: #F8F8F8;
}

.favorite_icon {
    color: #FF6767;
}

/*Joke*/

.joke {
    display: flex;
    margin-top: 10px;
    background: #F8F8F8;
    border-radius: 20px;
}

/*Comment icon*/

.comment_circle {
    display: flex;
    justify-content: center;
    flex: 0 0 40px;
    overflow: hidden;
    height: 40px;
    margin-right: 20px;
    border-radius: 50%;
    background: white;
}

.comment_icon {
    width: auto;
    height: 22px;
    margin-top: 25%;
    color: #ABABAB;
}

/*Joke content*/

.joke_content {
    width: 100%;
}

/*Joke ID*/

.id {
    color: #ABABAB;
    font-size: 10px;
    line-height: 14px;
}

/*Joke link*/

#link {
    font-size: 10px;
    line-height: 14px;
    background: url(data:image/png;base64,) no-repeat 100% 0;
    background-size: 14px 14px;
    padding-right: 19px;
    color: #8EA7FF;
}

/*Joke value*/

.joke_value {
    max-width: 540px;
    margin-top: 5px;
    margin-bottom: 7px;
    font-weight: normal;
    padding-top: 5px;
    padding-bottom: 10px;
    font-size: 18px;
    line-height: 26px;
}

/*Last update and Category*/

.update_category {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #ABABAB;
}

/*Joke category*/

.joke_category {
    background: #FFFFFF;
    border-radius: 6px;
    padding: 5px 20px;
    margin-right: -2px;
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #333333;
}

.joke_category:empty {
    display: none;
}

@media (max-width: 1240px) {
    .joke_category {
        padding: 5px 10px;
    }
}

@media (max-width: 430px) {
    .joke_value {
        max-width: calc(100vw - 140px);
    }
}

@media (max-width: 320px) {
    .joke_value {
        max-width: 180px;
        width: 100%;
    }
}
/*Category list*/

.categories_list {
    display: flex;
    flex-wrap: wrap;
}
/* RadioButton Container */

.select_option {
    display: flex;
    flex-direction: column;
}

/*Radio buttons takes from here https://codepen.io/hansmaad/pen/zBdzxp
 and here https://dev.to/lkopacz/create-custom-keyboard-accessible-radio-buttons-22eh
*/

.form-check-input {
    display: none;
}

.form-check-input:checked + label:before {
    border-color: #333333;
}

.form-check-input:checked + label:after {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.radio_label {
    display: inline-block;
    position: relative;
    padding: 0 30px;
    margin-bottom: 15px;
    cursor: pointer;
    font-weight: normal;
    font-size: 18px;
}

.radio_label:before, :after {
    position: absolute;
    content: '';
    border-radius: 50%;
    transition: all .3s ease;
    transition-property: border-color, -webkit-transform;
    transition-property: transform, border-color;
    transition-property: transform, border-color, -webkit-transform;
}

.radio_label:before {
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border: 2px solid #ABABAB;
}

.radio_label:after {
    top: 5px;
    left: 5px;
    width: 10px;
    height: 10px;
    -webkit-transform: scale(0);
            transform: scale(0);
    background: #333333;
}

*, *:before, *:after {
    box-sizing: border-box;
}

/*Radio button = Search*/

.search {
    display: block;
    margin-top: 10px;
    margin-bottom: 5px;
}

/*Search input*/

.search_input {
    width: 100%;
    padding: 8px 13px;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    border: 2px solid #333333;
    box-sizing: border-box;
    border-radius: 10px;
}

.search_input::-webkit-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.search_input:-ms-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.search_input::-ms-input-placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}

.search_input::placeholder {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #ABABAB;
}
/*Get a joke button*/

.btn {
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: white;
    margin-top: 20px;
    padding: 9px 38px;
    background: linear-gradient(92.01deg, #8EA7FF 0%, #7291FF 100%);
    border-radius: 10px;
    border: none;
}

.btn:hover {
    background: linear-gradient(92.01deg, #7291FF 0%, #8EA7FF 100%);
}
/*Menu for tablet and mobile*/

.side_fav_menu {
    display: none;
}

.side_fav_title {
    display: none;
}

@media (max-width: 960px) {
    .side_fav_menu {
        display: flex;
        z-index: 4;
        top: 40px;
    }

    .side_fav_icon {
        cursor: pointer;
        margin-right: 10px;
        background: #333;
        border-radius: 50%;
        padding: 10px 7px;
        width: 28px;
        height: 28px;
        position: relative;
        box-sizing: border-box;
    }

    .side_fav_icon-line{
        display: block;
        width: 14px;
        height: 2px;
        background: #fff;
        border-radius: 2px;
        position: absolute;
    }

    .first-line{
        top: 10px;
    }

    .second-line{
        bottom: 10px;
    }

    .menu_active .first-line{
        top: 13px;
        transition: 0.1s;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }

    .menu_active .second-line{
        bottom: 13px;
        transition: 0.1s;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }

    .side_fav_title {
        display: inline;
        font-size: 20px;
        line-height: 28px;
        color: #ABABAB;
    }
}
